import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const routes= [
  {path: '/', meta: {title: '加盟指南_加盟指南资讯，加盟指南知识，加盟指南频道，招商外包，招商外包服务公司'}, component: resolve => require(['../pages/home/home.vue'], resolve)},
  {path: '/list', meta: {title: '找项目'}, component: resolve => require(['../pages/list/list.vue'], resolve)},
  {path: '/rankingList', meta: {title: '排行榜'}, component: resolve => require(['../pages/rankingList/rankingList.vue'], resolve)},
  {path: '/consultDetail', meta: {title: '资讯详情'}, component: resolve => require(['../pages/consultDetail/consultDetail.vue'], resolve)},
  {path: '/detail', meta: {title: '项目详情'}, component: resolve => require(['../pages/list/detail.vue'], resolve)},
  {path: '/news', meta: {title: '行业资讯'}, component: resolve => require(['../pages/news/newsIndex.vue'], resolve)},
  {path: '/newsList', meta: {title: '行业资讯'}, component: resolve => require(['../pages/news/newsList.vue'], resolve)},
  {path: '/personal', meta: {title: '个人中心'}, component: resolve => require(['../pages/personal/personalIndex.vue'], resolve)},
  {path: '/message', meta: {title: '我的消息'}, component: resolve => require(['../pages/personal/message.vue'], resolve)},
  {path: '/focus', meta: {title: '我的关注'}, component: resolve => require(['../pages/personal/focus.vue'], resolve)},
  {path: '/collect', meta: {title: '我的收藏'}, component: resolve => require(['../pages/personal/collect.vue'], resolve)},
  {path: '/leaveMessage', meta: {title: '我的留言'}, component: resolve => require(['../pages/personal/leaveMessage.vue'], resolve)},
  {path: '/footmark', meta: {title: '我的足迹'}, component: resolve => require(['../pages/personal/footmark.vue'], resolve)},
  {path: '/discuss', meta: {title: '我的评论'}, component: resolve => require(['../pages/personal/discuss.vue'], resolve)},
  {path: '/feedback', meta: {title: '意见反馈'}, component: resolve => require(['../pages/personal/feedback.vue'], resolve)},
  {path: '/data', meta: {title: '资料设置'}, component: resolve => require(['../pages/personal/data.vue'], resolve)},
  {path: '/account', meta: {title: '账户中心'}, component: resolve => require(['../pages/personal/business/account.vue'], resolve)},
  {path: '/investorManage', meta: {title: '投资者管理'}, component: resolve => require(['../pages/personal/business/investorManage.vue'], resolve)},
  {path: '/leaveManage', meta: {title: '留言管理'}, component: resolve => require(['../pages/personal/business/leaveManage.vue'], resolve)},
  {path: '/evaluate', meta: {title: '智能评估'}, component: resolve => require(['../pages/evaluate/evaluate.vue'], resolve)},
  {path: '/evaluationReport', meta: {title: '智能评估'}, component: resolve => require(['../pages/evaluate/evaluationReport/evaluationReport.vue'], resolve)},
  {path: '/login', meta: {title: '登录'}, component: resolve => require(['../pages/personal/login.vue'], resolve)},
  {path: '/enter', meta: {title: '企业入驻'}, component: resolve => require(['../pages/personal/enter.vue'], resolve)},
  {path: '/aboutUs', meta: {title: '企业入驻'}, component: resolve => require(['../pages/home/aboutUs.vue'], resolve)}
]

const router = new Router({
  routes,
  mode: 'history'
});
router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
    //设置页面的title
  }
  next();
});

router.afterEach((to, from, next) => {
  window.scrollTo(0,0);
})
export default router;



