<template>
    <div class="common-header">
        <div class="body wrapper">
            <div class="left">
                欢迎来到加盟指南网！ 全国服务热线：400-619-8882
            </div>
            <div class="right">
              <div class="phoneVersion">
                 <span class="iconfont icon-phone"></span>
                 <span>手机版</span>
                <img src="../../static/image/wap_qrcode.jpg" class="QRcode" alt="手机二维码">
              </div>
              <div class="weChatVersion">
                  <span class="iconfont icon-weChat"></span>
                  <span>微信版</span>
                  <img src="../../static/image/wx_qrcode.jpg" class="QRcode" alt="微信二维码">
              </div>
              <div class="" >
                <router-link v-if="user" to="/personal"> <span class="name">您好，{{user.username}}</span> <span @click="logout">退出登陆</span> </router-link >
                <router-link  v-else to="/login">登陆</router-link >

              </div>
            </div>
        </div>
    </div>
</template>

<script>
  import api from '@/api/login-api'
    export default {
        name: "common-header",
        data(){
          return {
            user: ''
          }
        },
      watch: {
        $route(to,from){

          if(from.path == '/login') {
            this.user = JSON.parse(localStorage.getItem('user'));
          }
        }
      },
      created(){
          this.user = JSON.parse(localStorage.getItem('user'));
        },
        methods:{
          logout(){
              api.logout().then(e => {
                localStorage.removeItem("token");
                localStorage.removeItem("user");
                this.$router.push('/login');
              });
          }
        }
    }
</script>

<style scoped>
  .name{
    color: #5389F7
  }
  .common-header{
    height: 32px;
    width: 100%;
    background-color: #E3E4E5;
  }
  .body{
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 12px;
    color: #666;
    margin: auto;
  }
  .body .right{
    display: flex;
    align-items: center;
  }
  .body .right>div{
    display: flex;
    position: relative;
    height: 12px;
    line-height: 12px;
    padding:0 14px;
    border-right: 1px solid #666;
  }
  .body .right>div:hover{
    cursor: pointer;
    color: #579EFA;
  }
  .body .right>div:hover .QRcode{
    display: block;
  }
  .body .right>div:last-of-type{
    padding-right: 0;
    border: 0;
  }
  .QRcode{
    display: none;
    position: absolute;
    top: 18px;
    left: 18px;
    width: 65px;
    height: 65px;
    z-index: 100;

  }
  .iconfont{
    color: #999;
    margin-right: 5px;
  }
</style>



