const requestGet = (url = '', data = {}) => {
    // var result = axios.get(url, data);
  var result = request('get', url, data)
  return result
}

const requestPost = (url = '', data = {}) => {
  var result = request('post', url, data)
  return result
}
const uploadFile = (url = '', data = {}) => {
  return new Promise(function (resolve, reject) {
    var boundary = NumberUtils.uuid()
    var option = {
      headers: {'Content-Type': 'multipart/form-data;boundary=' + boundary},
      withCredentials: true
    }
    var formData = new FormData()
    for (var key in data) {
      if (ListUtils.isArray(data[key])) {
        for (var value of data[key]) {
          formData.append(key, value)
        }
      } else {
        formData.append(key, data[key])
      }
    }
    axios.post(url, formData, option).then((response) => {
      if (response.status === 200) {
        var result = response.data
        resolve(result)
      } else {
        reject(new Error(response.statusText))
      }
    }).catch((e) => {
      console.error('上传文件失败====', e)
      reject(e)
    })
  })
}
