const request = (type = 'get', url = '', data = {}) => {

  return new Promise(function (resolve, reject) {
    type = type.toLowerCase()
    var option = {
      url: url, method: type, transformRequest: [function (data) {
        let ret = ''
        for (let it in data) {
          if (ListUtils.isArray(data[it])) {
            data[it] && data[it].forEach((item) => {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(item) + '&'
            })
          } else {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
        }
        return ret
      }],      // 查询对象序列化函数
      paramsSerializer: function (params) {
        let ret = ''
        for (let it in params) {
          if (ListUtils.isArray(params[it])) {
            params[it] && params[it].forEach((item) => {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(item) + '&'
            })
          } else {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(params[it]) + '&'
          }
        }
        if (ret) {
          ret = ret.substring(0, ret.length - 1)
        }
        return ret
      }
    }
    const token = localStorage.getItem('token') || '';
    if (type === 'get') {
      option.params = data
      option.params.token = token;
    } else {
      option.data = data
      option.data.token = token;
    }
    option = beforeRequest(option);

    axios.request(option).then((response) => {
      if (response.data.code === 200) {
        var result = response.data.data;
        resolve(result)
      } else if(response.data.code === 301){
        /**
         * 未登陆 或者登陆状态失效
         */
        _this.$message.error(response.data.msg);
        setTimeout(e =>{
          _this.$router.push("/login");
        },800)
      }else{
        _this.$message.error(response.data.msg);
        reject(new Error(response.data.msg))
      }
    }).catch((e) => {
      reject(e)
    })

  })
}
export default {get: requestGet, post: requestPost, upload: uploadFile}





