/**
 * 引入fetch、baseUrl
 * @param params
 * @returns {*}
 */
import httpUtils from 'config/index'

class loginService {
  getImageCode(){
    return httpUtils.get("/index/login/imageCode");
  }
  getCode(params){
    return httpUtils.get("/index/login/get_code",params);
  }
  login(params){
    return httpUtils.get("/index/login/login",params);
  }
  logout(){
    return httpUtils.get("/index/login/logout");
  }
}
export default new loginService()



