<template>
  <div id="app">
    <common-header v-if="header_show"></common-header>
    <router-view v-on:header="header" v-on:footer="footer" />
    <common-footer v-if="footer_show"></common-footer>
  </div>
</template>

<script>
  import Header from './components/common-header'
  import Footer from './components/common-footer'
  import "./assets/font/iconfont.css"
export default {
  name: 'App',
  data(){
    return {
      header_show:true,
      footer_show:true,
    }
  },
  components: {
    'common-header':Header,
    'common-footer':Footer,
  },
  methods: {
    //是否显示头部
    header:function (bool) {
      this.header_show = bool;
    },
    //是否显示底部
    footer:function (bool) {
      this.footer_show = bool;
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
  font-size: 14px;
}
* {
  margin: 0;
  padding: 0;
  outline: none;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
  cursor: pointer;
  color: #333;
  outline: none;
}
a:hover {
  -webkit-transition: all 0.3s ease-out;
  -moz-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
a img, a:hover img {
  border: 0 none;
}
h1, h2, h3, h4, h5 {
  font-weight: normal;
}
input, button, select, textarea {
  outline: none;
  border: 0 none;
  font-family: "微软雅黑";
}
.wrapper {
  width: 1200px;
  margin: 0 auto;
  position: relative;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.clearfix:after {
  display: block;
  clear: both;
  content: "";
  visibility: hidden;
  height: 0;
}
.clearfix {
  zoom: 1
}
.clear {
  clear: both;
}
.grey_bg {
  background: #f5f8fe;
}
table {
  border-collapse: collapse;
  /*border-spacing: 1;*/
  border-spacing: 0;
}
.over {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.href_aaa {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 10;
}
.between{
   display: flex;
   flex-direction: row;
   justify-content: space-between;
 }
.around{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
</style>



